import { useState } from "react";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import { useAdmissionOngoingApplicationReviewSort } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";

const AdmissionReviewResuffleMenu = ({ onClose, appId, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState(false);
  const [state, setState] = useState("");
  const [admissionOngoingApplicationReviewSort] =
    useAdmissionOngoingApplicationReviewSort();

  const onStepToggle = () => {
    setStep((pre) => !pre);
  };
  const onAction = (val) => {
    if (appId) {
      if (val === "Alpha" || val === "Alpha_Last") {
        setState(val);
      }
      setDisabled((pre) => !pre);
      admissionOngoingApplicationReviewSort({
        appId: appId,
        sort_query: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <ModalMenu
      onClose={onClose}
      disabled={disabled}
      customStyle={{
        width: "18rem",
      }}
    >
      {/* <h6 onClick={() => onAction("Alpha")}>{t("alphabetical")}</h6> */}
      <h6 onClick={onStepToggle}>{t("alphabetical")}</h6>
      {step ? (
        <div>
          <TileWrapper
            customTileContainer={{
              marginBottom: "0.7rem",
            }}
          >
            {admissionIntakeType?.map((tile) => (
              <TileCard
                key={tile?.key}
                tile={tile}
                onTile={onAction}
                activeTile={state}
              />
            ))}
          </TileWrapper>
        </div>
      ) : null}
      <h6 onClick={() => onAction("Gender")}>{t("gender")}</h6>
      <h6 onClick={() => onAction("Gender_Alpha")}>{t("both")}</h6>
    </ModalMenu>
  );
};

export default AdmissionReviewResuffleMenu;

export const admissionIntakeType = [
  {
    key: "Alpha",
    name: "First Name",
  },
  {
    key: "Alpha_Last",
    name: "Surname",
  },
];
